<template>
  <div id="app">
    <div class="container">
    <StyleEditor ref="styleEditor" :code="currentStyle"></StyleEditor>
    <ResumeEditor ref="resumeEditor" :markdown="currentMarkdown" :enableHtml="enableHtml"></ResumeEditor>
    </div>
    <div class="btn-wrap">
      <div class="btn" @click="backBlog">返回博客</div>
    </div>
  </div>
</template>

<script>
  import StyleEditor from './components/StyleEditor'
  import ResumeEditor from './components/ResumeEditor'
  import './assets/reset.css'

  export default {
    name: 'app',
    components: {
      StyleEditor,
      ResumeEditor
    },
    data() {
      return {
        interval: 40,
        currentStyle: '',
        enableHtml: false,
        fullStyle: [
          `/*
* hello，我是李日发，一名前端开发工程师
* 接下来我将用动画的形式来介绍我自己
*/

/* 首先给所有元素加上过渡效果 */
* {
  transition: all .3s;
}
/* 白色背景太单调了，我们来点背景 */
html {
  background: rgb(0,43,54);
}
/***
 * 等等...
 */
body {
  color: white;
}
/*
* 这样看起来好多了
* 我们继续...
*/

.styleEditor {
  padding: .5em;
  border: 1px solid;
  margin: .5em;
  overflow: auto;
  width: 49vw; 
  height: 90vh;
}
/* 代码高亮 */
.token.selector{ color: rgb(133,153,0); }
.token.property{ color: rgb(187,137,0); }
.token.punctuation{ color: yellow; }
.token.function{ color: rgb(42,161,152); }

/* 加点 3D 效果呗 */
html{
  perspective: 1000px;
}
.styleEditor {
  position: fixed;
  left: 10px;
  top: 0;
  -webkit-transition: none;
  transition: none;
  -webkit-transform: rotateY(10deg) translateZ(-100px) ;
  transform: rotateY(10deg) translateZ(-100px) ;
}
.styleEditor:hover{
    box-shadow: 0px 0px 20px 5px rgba(255,255,255,0.4);
}
/* 接下来我给自己准备一个编辑器 */
.resumeEditor{
  position: fixed; right: 0; top: 0;
  padding: .5em;  margin: .5em;
  width: 49vw; height: 90vh;
  border: 1px solid;
  background: white; color: #222;
  overflow: auto;
}
/* 好了，我开始写简历了... */


`,
          `
/* 这个简历好像差点什么
 * 对了，这是 Markdown 格式的，我需要变成对 HR 更友好的格式
 * 简单，用开源工具翻译成 HTML 就行了
 */
`
          ,
          `
/* 再对 HTML 加点样式 */
.resumeEditor{
  padding: 2em;
}
.resumeEditor h2{
  display: inline-block;
  border-bottom: 1px solid;
  margin: 1em 0 .5em;
}
.resumeEditor ul,.resumeEditor ol{
  list-style: none;
}
.resumeEditor ul> li::before{
  content: '•';
  margin-right: .5em;
}
.resumeEditor ol {
  counter-reset: section;
}
.resumeEditor ol li::before {
  counter-increment: section;
  content: counters(section, ".") " ";
  margin-right: .5em;
}
.resumeEditor blockquote {
  margin: 1em;
  padding: .5em;
  background: #ddd;
}
`],
        currentMarkdown: '',
        fullMarkdown: `#李日发
----

男 | 33岁 rafa_im@163.com
10年工作经验 | 前端开发工程师 | 期望薪资：20-25K | 期望城市：深圳

技能
----

* 熟练掌握前端基础（JavaScript、CSS3、HTML5），能够快速实现Web端和移动端页面搭建。
* 熟悉原生及uni-app框架下的小程序开发，包括申请注册、项目配置、代码开发、审核发布一系列流程。
* 熟练使用 Vue 框架以及相关生态技术，能够独立完成项目搭建和项目部署，熟悉相关框架原理。
* 熟悉浏览器原理以及计算机网络相关技术，在性能优化方面有实践经验。
* 对前端工程模块化有一定理解，熟悉webpack、vite等打包工具及其日常开发配置，可以从0到1独立搭建项目，并优化构建流程。
* 了解Node.js 和 python 服务端语言，能够快速构建后端接口和处理服务器端逻辑，保证项目的顺利推进。
* 在多个项目中独立负责，具备设计和实施前端架构的能力。

工作经历
----

1. 深圳理想动力信息技术有限公司产研中心
2. 深圳蓝色速度科技有限公司研发中心
3. 众乐互娱平台部
4. 深圳市丽海弘金科技有限公司研发中心

项目经历
----

1.知买微信小程序
2.高佣H5
3.运营管理系统
4.企鹅电竞官网重构
5.不思议迷宫微社区
6.Itrade 量化运维系统

链接
----

* [GitHub](https://github.com/lirifa)
* [我的文章](https://lirifa.github.io)

`
      }
    },
    created() {
      this.makeResume()
    },

    methods: {
      makeResume: async function () {
        await this.progressivelyShowStyle(0)
        await this.progressivelyShowResume()
        await this.progressivelyShowStyle(1)
        await this.showHtml()
        await this.progressivelyShowStyle(2)
      },
      showHtml: function () {
        return new Promise((resolve, reject) => {
          this.enableHtml = true
          resolve()
        })
      },
      progressivelyShowStyle(n) {
        return new Promise((resolve, reject) => {
          let interval = this.interval
          let showStyle = (async function () {
            let style = this.fullStyle[n]
            if (!style) { return }
            // 计算前 n 个 style 的字符总数
            let length = this.fullStyle.filter((_, index) => index <= n).map((item) => item.length).reduce((p, c) => p + c, 0)
            let prefixLength = length - style.length
            if (this.currentStyle.length < length) {
              let l = this.currentStyle.length - prefixLength
              let char = style.substring(l, l + 1) || ' '
              this.currentStyle += char
              if (style.substring(l - 1, l) === '\n' && this.$refs.styleEditor) {
                this.$nextTick(() => {
                  this.$refs.styleEditor.goBottom()
                })
              }
              setTimeout(showStyle, interval)
            } else {
              resolve()
            }
          }).bind(this)
          showStyle()
        })
      },
      progressivelyShowResume() {
        return new Promise((resolve, reject) => {
          let length = this.fullMarkdown.length
          let interval = this.interval
          let showResume = () => {
            if (this.currentMarkdown.length < length) {
              this.currentMarkdown = this.fullMarkdown.substring(0, this.currentMarkdown.length + 1)
              let lastChar = this.currentMarkdown[this.currentMarkdown.length - 1]
              let prevChar = this.currentMarkdown[this.currentMarkdown.length - 2]
              if (prevChar === '\n' && this.$refs.resumeEditor) {
                this.$nextTick(() => this.$refs.resumeEditor.goBottom())
              }
              setTimeout(showResume, interval)
            } else {
              resolve()
            }
          }
          showResume()
        })
      }
      ,
      backBlog() {
        // 判断是否从 博客跳转过来，是的话就返回博客，跳转页面到博客
        console.log(document.referrer)
        if(document.referrer && document.referrer.indexOf('lirifa.github.io')!==-1){
          window.history.back()
        } else {
          window.location.href = 'https://lirifa.github.io'
        }

      }
    }
  }

</script>

<style scoped>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  html {
    min-height: 100vh;
  }
  *{
    box-sizing: border-box;
  }
</style>
